//Import general styling
@import "./style/general/typography.scss";

//Import file styling

//Colors
$primary-color: #ff0000;
$secondary-color: #ffff00;

*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.webgl {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 0;
  outline: none;
}

//Increase width of Pane
.tp-dfwv {
  min-width: 360px;
}

.grid-div {
  float: left;
  width: 200px;
  height: 200px;
}
